import axios from 'axios'


const apiUrl = 'http://localhost:8000/blog/'

const getURL = () => {

    return axios.get(apiUrl)
}

const postURL = (data) => {
    return axios.post(apiUrl, data)
}

const editURL = (id, data) => {
    return axios.post(apiUrl.concat(id), data)
}

const deltURL = (id) => {
    return axios.post(apiUrl.concat(id))
}

const Network = {
    'get': getURL,
    'post': postURL,
    'edit': editURL,
    'delete': deltURL
}

export default Network
